<template>
    <div class="table">
        <div class="container">
            <div class="handle-box">
                <!-- <el-select v-model="select_cate" placeholder="筛选省份" class="handle-select mr10">
                    <el-option key="1" label="广东省" value="广东省"></el-option>
                    <el-option key="2" label="湖南省" value="湖南省"></el-option>
                </el-select>
                <el-input v-model="select_word" placeholder="筛选关键词" class="handle-input mr10"></el-input> -->
                <!-- <el-button type="primary" icon="search">搜索</el-button> -->
                <el-button type="success" icon="search" @click="addClick">新增</el-button>
            </div>
            <el-table :data="list" border style="width: 100%" v-loading="listLoading" @row-dblclick="handleDoubleClick" highlight-current-row>
                <el-table-column fixed prop="companycode" width="280" label="公司代码">
                </el-table-column>
                <el-table-column prop="cname" label="公司名称">
                </el-table-column>
                <el-table-column prop="tel" label="公司电话">
                </el-table-column>
                <el-table-column prop="address" label="公司地址">
                </el-table-column>
                <el-table-column prop="creatid" label="创建者">
                </el-table-column>
                <el-table-column label="创建日期">
                    <template slot-scope="scope">
                        <span>{{ scope.row.creatdate | parseTime('{y}-{m}-{d} {h}:{i}') }}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="updaid" label="修改者">
                </el-table-column>
                <el-table-column label="修改日期">
                    <template slot-scope="scope">
                        <span>{{ scope.row.updadate | parseTime('{y}-{m}-{d} {h}:{i}') }}</span>
                    </template>
                </el-table-column>
                <el-table-column fixed="right" label="操作">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="success" size="small">编辑</el-button>
                        <!-- <el-button type="text" size="small">编辑</el-button> -->
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" @prev-click='prevClick' @next-click='nextClick' background layout="total, sizes, prev, pager, next, jumper" :total='total' :page-sizes="[10,15,20,50]" :page-size="size" :current-page="pageNum">
                </el-pagination>
            </div>
        </div>

        <el-dialog title="公司详细信息" :visible.sync="editDialog" width="800px">
            <el-form ref="form" :model="company" label-width="170px" margin-right="100px">
                <el-form-item label="公司唯一编号(不可修改)">
                    <el-input v-model="company.rowid" :disabled='true'></el-input>
                </el-form-item>
                 <el-form-item label="公司代码(设置不可修改)">
                    <el-input v-model="company.companycode" ></el-input>
                </el-form-item>
                <el-form-item label="公司名称">
                    <el-input v-model="company.cname"></el-input>
                </el-form-item>
                <el-form-item label="公司电话">
                    <el-input v-model="company.tel"></el-input>
                </el-form-item>
                <el-form-item label="公司地址">
                    <el-input v-model="company.address"></el-input>
                </el-form-item>
                <el-form-item label="备注信息">
                    <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 6}" placeholder="请输入内容" v-model="company.remark">
                    </el-input>
                </el-form-item>

                <el-row>
                    <el-col :span="12">
                        <el-form-item label="创建者">
                            <el-input v-model="company.creatid" :disabled='true'></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="创建日期">
                            <el-date-picker label="创建日期" v-model="company.creatdate" :disabled='true'>
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="修改者">
                            <el-input v-model="company.updaid" :disabled='true'></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="修改日期">
                            <el-date-picker v-model="company.updadate" :disabled='true'></el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialog = false">取 消</el-button>
                <el-button type="primary" @click="updata">确 定</el-button>
            </span>
        </el-dialog>

        <el-dialog title="新增公司" :visible.sync="addDialog" width="60%">
            <el-form ref="form" :model="company" label-width="150px" margin-right="100px">
                <el-form-item label="公司名称">
                    <el-input v-model="company.cname"></el-input>
                </el-form-item>
                     <el-form-item label="公司代码">
                    <el-input v-model="company.companycode"></el-input>
                </el-form-item>
                <el-form-item label="公司电话">
                    <el-input v-model="company.tel"></el-input>
                </el-form-item>
                <el-form-item label="公司地址">
                    <el-input v-model="company.address"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialog = false">取 消</el-button>
                <el-button type="primary" @click="add">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 删除提示框 -->
        <!-- <el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
      <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
      <span slot="footer" class="dialog-footer"> -->
        <!-- <el-button @click="delVisible = false">取 消</el-button> -->
        <!-- <el-button type="primary" @click="deleteRow">确 定</el-button> -->
        <!-- </span>
    </el-dialog> -->
    </div>
</template>

<script>
import {
  fetchCompanyList,
  companyAdd,
  //   companyDetail,
  companyUpdate
  //   companyDelete
} from '@/api/company/company.js'

export default {
  data () {
    return {
      editDialog: false,
      addDialog: false,
      company: {
        companycode: '',
        rowid: '',
        cname: '',
        tel: '',
        address: '',
        creatid: '',
        creatdate: '',
        updaid: '',
        updadate: '',
        remark: ''
      },
      listQuery: {
        page: 0, // 当前页
        size: 10 // 一页多少
      },
      listLoading: true,
      list: [],
      pageNum: 0, // 当前页
      pages: 0, // 总页数
      pageSize: 0, // 每页的数量
      size: 0, // 当前页的数量
      total: 0 // 总记录数
    }
  },
  created () {
    this.getList()
  },
  computed: {
    // creatdate: {
    //   get: () => this.form.creatdate,
    //   set: (value) => this.form.creatdate
    // }
  },
  methods: {
    // 双击添加
    addClick () {
      this.addDialog = true
      this.company = {
        cname: '',
        tel: '',
        address: '',
        creatid: '',
        creatdate: '',
        updaid: '',
        updadate: '',
        remark: ''
      }
    },
    // 新增公司
    add () {
      this.addDialog = false
      companyAdd(this.company).then(response => {
        this.getList()
        console.log('组件内数据' + response)
        this.$message({
          showClose: true,
          message: '添加成功',
          type: 'success'
        })
      })
    },
    // 双击弹出
    handleDoubleClick (row, event) {
      console.log(row)
      console.log(event)
      this.editDialog = true
      this.company = row
    },
    //   修改信息弹出
    handleClick (company) {
      this.editDialog = true
      this.company = company
    },
    // 修改数据
    updata () {
      this.editDialog = false
      companyUpdate(this.company).then(response => {
        console.log('组件内数据' + response)
        this.$message({
          showClose: true,
          message: '修改成功',
          type: 'success'
        })
      })
    },
    // 获取列表
    getList () {
      this.listLoading = true
      fetchCompanyList(this.listQuery)
        .then(response => {
          this.list = response.data.list
          this.pages = response.data.pages
          this.pageSize = response.data.pageSize
          this.total = response.data.total
          this.pageNum = response.data.pageNum
          console.log('获取列表')
          console.log(response)
          this.listLoading = false
          // this.total = response.data.data.total
          // setTimeout(() => {
          //   this.listLoading = false
          // }, 1.5 * 1000)
        })
        .catch(error => {
          console.log('组件内接受错误' + error)
          this.listLoading = false
        })
    },
    // 改变每一页多少条数据
    handleSizeChange (val) {
      this.listQuery.size = val
      this.getList()
      console.log(`每页 ${val} 条`)
    },
    // 改变当前页
    handleCurrentChange (val) {
      this.listQuery.page = val
      this.getList()
      console.log(`当前页: ${val}`)
    },
    // 上一页
    prevClick () {
      ++this.listQuery.size
      this.getList()
    }, // 下一页
    nextClick () {
      --this.listQuery.size
      this.getList()
    }
  }
}
</script>

<style scoped>
.handle-box {
  margin-bottom: 10px;
}

.handle-select {
  width: 120px;
}
.handle-input {
  width: 300px;
  display: inline-block;
}
.del-dialog-cnt {
  font-size: 16px;
  text-align: center;
}
.table {
  width: 100%;
  font-size: 14px;
}
.red {
  color: #ff0000;
}
</style>



